import React, { useState, useEffect } from 'react'
import { reactLocalStorage } from 'reactjs-localstorage';
import { db } from "../../firebase";

const Ghostel = () => {
    const Userdata = JSON.parse(reactLocalStorage.get('userdata'))
    const [hostel, setHostel] = useState([]);
    const [loading, setLoading] = useState(false);
    const [old,setOld]=useState(undefined);
    const [update,setUpdate]=useState("");

    const userid = Userdata.email.substring(0, Userdata.email.indexOf('@'));

     //Have Old Room?
     useEffect(()=>{
        db.collection("StudentDatas").doc("Booked")
        .get()
        .then(function (doc) {
            if (doc.exists) {
                setOld(doc.data()[userid]);
            } else {
                // doc.data() will be undefined in this case
                console.log("No old datas!!");
            }
        }).catch(function (error) {
            console.log("Error getting student old room details:", error);
        });
    },[update])

    //Get Hostel Datas
    useEffect(() => {
        const getData = async () => {
            setLoading(true);
            await db.collection('HostelBlocks').onSnapshot(snapshot => {
                setHostel(snapshot.docs.map(docs => docs.data()))
            });
            setLoading(false);
        }
        getData();
    }, [update]);

    function choosed(block, roomnum) {
        const userroom = "G" + block + ":" + roomnum;
        reactLocalStorage.set(userid, userroom);
        setUpdate(userroom);
        db.collection("StudentDatas").doc("Booked").update({ [userid]:{"Hostel":"Girls","Block":block,"Room":roomnum}});
        hostel[1][block][roomnum - 1] = false;
        

        //Choose Room
        var ref = db.collection("HostelBlocks").doc("Girls");
        ref.update({
            [block]: hostel[1][block]
        })

        if(old)
        {
        hostel[1][old.Block][old.Room-1] = true;
        ref.update({
            [old.Block]:  hostel[1][old.Block]
        })
        //console.log("OLD ROOM: "+old.Block+" "+old.Room);
        //console.log("OLD ROOM: "+hostel[1][old.Block]);
         }
        //  else
        //  console.log("NO OLD ROOM!!");
         
         alert("NEW ROOM ASSIGNED: "+block+" "+roomnum);
    }

    function showrooms(b_name) {
        if (loading) {
            document.getElementById("rids").innerText = "Loading Data...";
        }
        else {
            const mainblock = document.getElementById("rids");
            mainblock.innerHTML = "";
            const tot = hostel[1][b_name];

            for (let i = 1; i <= tot.length; i++) {
                const r = document.createElement("button");
                r.innerText = "Room-" + i;
                r.setAttribute("id", tot[i - 1] ? "roomid" : "roomid2");
                r.setAttribute(tot[i - 1] ? "enabled" : "disabled", true);
                r.addEventListener("click", function () {
                    choosed(b_name, i);
                });
                mainblock.appendChild(r);
            }
        }

        //alert("Room Changed!!"+tot.length);
    }
    return (
        <div className="hostelbox">
            <div className="hostel_header">
                <i id="ico" class="fas fa-female"></i>
                <h1>GIRL'S HOSTEL!!</h1>
                <i id="ico" class="fas fa-female"></i>
            </div>
            <div className="blocks_col">
                <button id="blkbtn" onClick={() => showrooms("A")}>Block-A</button>
                <button id="blkbtn" onClick={() => showrooms("B")}>Block-B</button>
                <button id="blkbtn" onClick={() => showrooms("C")}>Block-C</button>
                <button id="blkbtn" onClick={() => showrooms("D")}>Block-D</button>
                <button id="blkbtn" onClick={() => showrooms("E")}>Block-E</button>
            </div>

            <div id="rids">
                Choose Block To See Available Rooms!!
            </div>
        </div>
    )
}

export default Ghostel
